import React from 'react'
import './css/Header.css'
import SearchIcon from '@mui/icons-material/Search';
import InboxIcon from '@mui/icons-material/Inbox';
import {Avatar} from '@mui/material';

const Header = ({ setSearchQuery }) => {
  return (
    <header>
      <div className='header-container'>
        <div className='header-left'>
        <img src={require('./logo.png')} alt="Logo" width="50px" height="50px" />
        </div>
        <div className='header-middle'>
          <div className='header-search-container'>
            
            <input type='text' placeholder = 'Search...' onChange={(e) => setSearchQuery(e.target.value)}/>
            <SearchIcon />
          </div>
          <h3>Products</h3>
        </div>
        <div className='header-right'>
          <div className='header-right-container'>
          <img src={require('./award.png')} alt="Logo" width="32px" height="32px" />
          <img src={require('./message.png')} alt="Logo" width="32px" height="32px" />
          <img src={require('./chat.png')} alt="Logo" width="32px" height="32px" />
          <img src={require('./DP.png')} alt="Logo" width="32px" height="32px" />
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header