import React, { useEffect } from 'react';
import lottie from 'lottie-web';
import './StackOverflow/css/loader.css';
import animationData from './StackOverflow/lottie.json'; // Replace with actual path

const Loader = ({ setLoading }) => {
  useEffect(() => {
    const animationContainer = document.getElementById('lottie-animation');
    if (!animationContainer) return;

    const animation = lottie.loadAnimation({
      container: animationContainer,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData, // Use imported JSON data
    });

    const timer = setTimeout(() => {
      setLoading(false);
    }, 3000);

    return () => {
      clearTimeout(timer);
      animation.destroy();
    };
  }, [setLoading]);

  return (
    <div className="loader">
      <div id="lottie-animation" className="lottie-container"></div>
    </div>
  );
};

export default Loader;
